.canvas-preview {
    background-color: #fff;
}

.stage-edit-container {
    
}

.stage-edit-header {
  background-color: #4d4d4d;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-align: center;
      align-items: center;
  padding: 5px 10px;
}

.stage-edit-header .stage-icon {
  width: 100%;
  max-width: 23px;
  max-height: 23px;
  margin-right: 0.5em;
}

.stage-edit-header h5 {
  color: #fff;
  font-size: 0.9em;
  font-weight: 300;
  text-transform: uppercase;
  margin: 0;
}

.stage-legend {
  font-size: 0.9em;
  color: #666;
}
.list-items {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;

  list-style-type: none;
  padding: 0;
  margin: 5px 0;
}

.list-items li {
  width: 20%;
  cursor: pointer;
  min-height: 50px;
}

.list-items li div {
  max-width: 48px;
  height: 48px;
  border-radius: 50%;
  margin: 1px auto;
}

.list-items li div.active {
  border: 3px dotted #f00;
  max-width: 42px;
  height: 42px;
  -webkit-box-shadow: 0 0 0 1px inset #333;
          box-shadow: 0 0 0 1px inset #333;
}

.button-download {
  padding: 8px 10px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;

  background: #10ac84;
  color: #fff;
  font-size: 0.85em;
  font-weight: 300;
  text-decoration: none;
  border-radius: 4px;
  text-transform: uppercase;

  max-width: 130px;
  margin-left: auto;
  margin-right: auto;
}

.button-download svg {
  margin-right: 0.5em;
}


